import ScreenContainer from "@/components/ui/ScreenContainer";
import ReduxProvider from "@/store/Provider";
import { cn } from "@/utils/classNameMerge";
import type { Metadata } from "next";
import { Work_Sans } from "next/font/google";
import "./globals.css";
import { Auth0Provider } from '@auth0/auth0-react';

const workSans = Work_Sans({
  variable: "--font-work-sans",
  weight: ["100", "300", "400", "500", "700", "800", "900"],
  subsets: ["latin"],
  display: "swap",
});

export const metadata: Metadata = {
  title: "Habit Square",
  description: "Simple habit tracker",
};

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <Auth0Provider
      domain="flashcards-bh11.us.auth0.com"
      clientId="nNCxQ3oJI9VhMzukmhHLij2rhiB46IIx"
      authorizationParams={{
        redirect_uri: window.location.origin
      }}
    >
      <ReduxProvider>
        <html lang="en">
          <head>
            <meta
              name="google-adsense-account"
              content="ca-pub-1198833545019540"
            />
            <meta
              name="viewport"
              content="width=device-width, initial-scale=1.0"
            />
          </head>
          <body className={cn(workSans.variable, "font-workSans antialiased")}>
            <ScreenContainer className="bg-base text-text flex flex-col items-center justify-between p-[2px] overflow-y-auto scrollbar-hide">
              {children}
            </ScreenContainer>
          </body>
        </html>
      </ReduxProvider>
    </Auth0Provider>
  );
}
